<template>
    <div class="p-2">
        <h2 class="p-1">
            <b-badge>{{ get_time_str() }}</b-badge>
            {{ map_name }}
        </h2>
        <b-table striped hover :items="items" :fields="fields">

        </b-table>
    </div>
</template>

<script>
export default {
    data() {
        return {
            fields: [
                {
                    key: 'job',
                    sortable: false
                },
                {
                    key: 'name',
                    sortable: false
                },

                {
                    key: 'dps',
                    sortable: true,
                },

                {
                    key: 'dps_m',
                    label: 'dps(1min)',
                    sortable: true,
                }
            ],
            items: [],
            min_record_time: 0,
            last_record_time: 0,
            map_name: "?",
        }
    },
    methods: {
        set_data: function (min_record_time, last_record_time, map_name, data) {
            this.items = data
            if (min_record_time / 1000 !== this.min_record_time)
                this.min_record_time = min_record_time / 1000;
            if (last_record_time / 1000 !== this.last_record_time)
                this.last_record_time = last_record_time / 1000;
            if (map_name !== this.map_name)
                this.map_name = map_name;
        },
        get_time_str() {
            let d = this.last_record_time - this.min_record_time;
            return (d / 60).toFixed(0).padStart(2, "0") + ":" + (d % 60).toFixed(0).padStart(2, "0")
        }
    },
    mounted() {
        window['set_data'] = this.set_data
    }
}
</script>
